/* CSS Document */
html {
  font-size: 10px;
}

body {
  font-size: 1.6rem;
}

p,
img {
  max-width: 100%;
  height: auto;
}

.left,
.left img {
  max-width: 50%;
  height: auto;
  float: left;
  margin-right: 12px;
  margin-bottom: 12px;
}

.right,
.right img {
  max-width: 50%;
  height: auto;
  float: right;
  margin-left: 12px;
  margin-bottom: 12px;
}

.center,
.center img,
.leftAlone,
.leftAlone img {
  max-width: 100%;
  height: auto;
  float: none;
  margin-bottom: 20px;
  display: block;
}

.ss-broken {
  background-color: red;
  color: white;
  padding: 1px;
}

a.ss-broken {
  text-decoration: underline;
}

